body {
  margin: 0;
}

.root {
  font-family: sans-serif;
  text-align: center;
  position: relative;
}

.wrapper {
  height: 1080px;
  background-repeat: repeat-y;
  background-size: cover;
  background-position: center 0;
  position: relative;
}

.wrapper1 {
  background-image: url(./assets/images/Slide_1_sheme_2.png);
}

.wrapper2 {
  background-image: url(./assets/images/Slide_2_sheme_2.png);
}

.wrapper3 {
  background-image: url(./assets/images/Slide_3_sheme_2.png);
}

.target {
}

.ball {
  height: 40px;
  width: 40px;
  background-color: red;
  border-radius: 50%;
  opacity: 0.5;
  will-change: margin-left;
  -webkit-transform: translate3d(0, 0, 0);
}

.ball.isFixed {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
  z-index: 1;
}

.spacer {
  height: 70vh;
}

.trigger {
  height: 1px;
  background-color: #000;
}

@media (max-width: 640px) {
  .wrapper {
    height: 506px;
  }
  .ball {
    height: 20px;
    width: 20px;
  }
}
